body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ), url(/starwarsapi/static/media/starWars.54a2b1ca.jpg) no-repeat center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@font-face {
  font-family: StarWars;
  src: url(/starwarsapi/static/media/Starjout.f72f509a.ttf) format("truetype");
}

@font-face {
  font-family: StarWars2;
  src: url(/starwarsapi/static/media/Starjedi.cc3019aa.ttf) format("truetype");
}


@font-face {
  font-family: Starjedi2;
  src: url(/starwarsapi/static/media/Starjedi.cc3019aa.ttf) format("truetype");
}



.Spinner__loader___24PGX,
.Spinner__loader___24PGX:before,
.Spinner__loader___24PGX:after {
  background: #ffffff;
  -webkit-animation: Spinner__load1___23zyj 1s infinite ease-in-out;
  animation: Spinner__load1___23zyj 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.Spinner__loader___24PGX {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.Spinner__loader___24PGX:before,
.Spinner__loader___24PGX:after {
  position: absolute;
  top: 0;
  content: '';
}
.Spinner__loader___24PGX:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.Spinner__loader___24PGX:after {
  left: 1.5em;
}
@-webkit-keyframes Spinner__load1___23zyj {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes Spinner__load1___23zyj {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

